@font-face {
  font-family: 'MyFont';
  src: url(./font/futura.ttf) format('truetype');
}

html {
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background: radial-gradient(rgb(255, 255, 193), #ffff99);
}

.title {
  position: relative;
  top: 100px;
  justify-content: center;
  align-items: center;
  font-size: 70px;
  font-family: 'myfont', sans-serif;
  text-align: center;
}

.container {
  display: flex;
  align-items: center;
  width: fit-content;
  margin: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

a {
  width: 70px;
  height: 70px;
  margin: 30px;
}

img {
  width: 100%;
  height: 100%;
}

.icons {
  color: black;
  font-size: 70px;
  margin: 0;
}

@media screen and (max-width: 767px) {
  .title {
    top: 150px;
    justify-content: center;
    align-items: center;
    font-size: 40px;
    text-align: center;
  }

  .container {
    display: flex;
    width: fit-content;
    margin: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }

  a {
    width: 55px;
    height: 55px;
    margin: 15px;
  }

  img,
  i::before {
    margin: 0px;
  }

  .icons {
    font-size: 55px;
    margin: 0px;
  }
}
